import produce from "immer";
import { buildBead } from "../../editorObjects/Bead";
import { AddObjects, AppEventType, GroupObjects } from "../AppEventType";
import { BaseEventHandler } from "../BaseHandlers/BaseEventHandler";
import { setStore } from "../store";
import { AddObjectsHistory, GroupObjectsHistory } from "../AppHistory";
import {
  addObjectByObjectType,
  triggerDeleteObjectIds,
} from "../../threejs/eventEmitterHandlers";
import { AppObject, ObjectTypes } from "../../editorObjects/Editor";
import { buildRegion } from "../../editorObjects/Region";
import { buildGroup } from "../../editorObjects/Group";
import { buildPreviewProject3D } from "../../editorObjects/PreviewProject3D";
import { refreshReactState } from "../refreshReactState";
import { StoreObjectHandler } from "../BaseHandlers/StoreObjectHandler";
import dayjs from "dayjs";
import * as THREE from "three";
import { getStateObjectFromObjectId } from "../../editorObjects/editorGetters";
import { appThree } from "../../threejs/renderThreejs";

export class GroupObjectsHandler extends StoreObjectHandler<GroupObjects> {
  run() {
    const groupIds: string[] = [];
    const idsWithoutGroup: string[] = [];
    const idsInGroup: string[] = [];
    const added: AppObject[] = [];
    const removed: AppObject[] = [];
    // const hitoryObjects: AppObject[] = [];
    let newStore = produce(this.store, (draft) => {
      let groupCount: any = 0;
      let inGroupCount: any = 0;
      let noGroupCount: any = 0;
      this.event.payload.objects.forEach((obj) => {
        if (!obj) {
          return;
        }
        if (draft.objects[obj.id!] === undefined) {
          return;
        }
        const isGroup = draft.objects[obj.id!].objectType === ObjectTypes.Group;
        const isInGroup =
          draft.objects[obj.id!].groupId !== undefined &&
          draft.objects[obj.id!].groupId!.length > 0;
        if (isGroup) {
          groupCount = groupCount + 1;
          groupIds.push(obj.id!);
        } else if (isInGroup) {
          inGroupCount = inGroupCount + 1;
          idsWithoutGroup.push(obj.id!);
          idsInGroup.push(obj.id!);
        } else {
          noGroupCount = noGroupCount + 1;
          idsWithoutGroup.push(obj.id!);
        }
      });

      groupIds.forEach((id) => {
        if (draft.objects[id] === undefined) {
          return;
        }
        removed.push(draft.objects[id]);
        delete draft.objects[id];
      });

      const bbox = new THREE.Box3();

      idsWithoutGroup.forEach((id) => {
        const obj = draft.objects[id];
        const name = getStateObjectFromObjectId(obj.id, obj.objectType);
        if (name === undefined) {
          return;
        }
        const object = appThree.scene.getObjectByUserDataProperty("name", name);
        if (object === undefined) {
          return;
        }
        bbox.expandByObject(object);
      });

      const w = bbox.max.x - bbox.min.x;
      const h = bbox.max.y - bbox.min.y;
      const newGroup = buildGroup({
        position: {
          x: bbox.min.x + w / 2,
          y: bbox.min.y + h / 2,
        },
        width: w,
        height: h,
        childrenIds: idsWithoutGroup,
      });
      draft.objects[newGroup.id] = newGroup;
      added.push(newGroup);

      idsInGroup.forEach((id) => {
        draft.objects[id].groupId = newGroup.id;
      });

      draft.selectedIds = [newGroup.id, ...idsWithoutGroup];
    });

    if (this.saveToHistory) {
      const historyEvent: GroupObjectsHistory = {
        do: this.event,
        redo: this.event,
        undo: {
          name: AppEventType.UngroupObjects,
          payload: { objects: added },
        },
        createdAt: dayjs().toISOString(),
      };
      this.history.push(historyEvent);
    }
    newStore = this.getStoreWithHistory(newStore);

    this.event.payload.objects.forEach((object) => {
      this.selectObjectOnThreeScene(object as any, false);
    });

    triggerDeleteObjectIds(
      this.event.payload.objects.map((obj) => {
        return { id: obj.id!, objectType: obj.objectType! };
      })
    );

    idsWithoutGroup.forEach((id) => {
      addObjectByObjectType(newStore.objects[id]);
    });

    added.forEach((obj) => {
      addObjectByObjectType(newStore.objects[obj.id]);
    });

    newStore.selectedIds.forEach((id) => {
      this.selectObjectOnThreeScene(newStore.objects[id], true);
    });

    newStore = this.getNewStoreWithTopBarPosition(newStore);
    setStore(newStore);
    refreshReactState(newStore);
  }
}
